import styled from "styled-components";
import { TiZoomOutline } from "react-icons/ti";
import { AnimatePresence, motion, useAnimation, useScroll } from "framer-motion";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";

const Nav = styled(motion.nav)`
    color: white;
    z-index: 100;
    position: fixed;
    top: 0;
    width: 100%;
    height: 64px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
    padding: 8px 32px;
    @media all and (max-width: 768px){
        //브라우저 창 width가 768px보다 작아지는 순간부터 적용
        //모바일
        padding: 8px 16px;
    }
`;
const UList = styled.ul`
    display: flex;
    align-items: center;
`;
const List = styled.li`
    color: ${(props) => props.theme.white.darker};
    transition: color 0.3s ease-in-out;
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: row;
    margin-right: 32px;
    @media all and (max-width: 768px){
        //브라우저 창 width가 768px보다 작아지는 순간부터 적용
        //모바일
        margin-right: 16px;
    }
    cursor: pointer;
    &:hover{
        color: ${(props) => props.theme.white.lighter};
    }
    a{
        color: inherit;
        text-decoration: none;
    }
`;
const Logo = styled(motion.svg)`
    fill: ${(props) => props.theme.red};
    width: 112px;
    height: 56px;
    @media all and (max-width: 768px){
        //브라우저 창 width가 768px보다 작아지는 순간부터 적용
        //모바일
        width: 72px;
        height: 36px;
    }
`;
const Circle = styled(motion.span)`
  position: absolute;
  width: 5px;
  height: 5px;
  border-radius: 5px;
  bottom: -8px;
  left: 0;
  right: 0;
  margin: 0 auto;
  background-color: ${(props) => props.theme.white.darker};
cursor: default;
`;
const Wrapper = styled(motion.form)`
    background-color: rgba(256,256,256,0.1);
    border: 1px solid ${(props) => props.theme.white.darker};
    display: flex;
    width: 200px;
    @media all and (max-width: 768px){
        //브라우저 창 width가 768px보다 작아지는 순간부터 적용
        //모바일
        width: 160px;
    }
    @media all and (max-width: 320px){
        //브라우저 창 width가 768px보다 작아지는 순간부터 적용
        //모바일
        width: 100px;
    }
`;
const Input = styled(motion.input)`
    width: 100%;
    transform-origin: right-center;
    background-color: rgba(0,0,0,0);
    color: ${(props) => props.theme.white.darker};
    font-size: 14px;
    padding: 8px 4px;
    border: none;
    outline: none;
    &:focus{
        border: none;
        outline: none;
    }
`;
const Icon = styled(motion.button)`
    display: flex;
    align-items: center;
    background-color: rgba(0,0,0,0);
    color: ${props=>props.theme.white.lighter};
    padding: 0px;
    cursor: pointer;
    border: 0px;
`;

const searchVar = {
    initial: {
        x: 100,
        scaleX: 0.1,
    },
    click: {
        x: 0,
        scaleX: 1,
        transition: {
            duration: 0.5,
        }
    },
    exit: {
        x: 100,
        scaleX: 0,
        transition: {
            duration: 0.5,
        }
    }
}

interface IForm{
    keyword: string;
}

function Header() {
    //react-hook-form
    const {register, handleSubmit, watch} = useForm<IForm>();
    const location = useLocation().pathname;
    const [searchState, setSearchState] = useState(false);
    const { scrollY } = useScroll();
    const navAnimation = useAnimation();
    const navigate = useNavigate();
    const onSearch = ({keyword}:IForm) => {
        if(keyword.length<1){
            setSearchState(cur => !cur);
            return;
        }else{
            navigate(`/search/${keyword}`);
        }
    }
    useEffect(() => {
        scrollY.onChange(() => {
            if (scrollY.get() < 80) {
                navAnimation.start({
                    backgroundColor: "rgba(0,0,0,0)"
                })
            } else {
                navAnimation.start({
                    backgroundColor: "rgba(0,0,0,1)"
                })
            }
        });
    }, []);
    return <Nav animate={navAnimation} initial={{ backgroundColor: "rgba(0,0,0,1)" }}>
        <UList>
            <List>
                <Link to={'/'}>
                    <Logo
                        xmlns="http://www.w3.org/2000/svg"
                        width="1024"
                        height="276.742"
                        viewBox="0 0 1024 276.742"
                    >
                        <motion.path d="M140.803 258.904c-15.404 2.705-31.079 3.516-47.294 5.676l-49.458-144.856v151.073c-15.404 1.621-29.457 3.783-44.051 5.945v-276.742h41.08l56.212 157.021v-157.021h43.511v258.904zm85.131-157.558c16.757 0 42.431-.811 57.835-.811v43.24c-19.189 0-41.619 0-57.835.811v64.322c25.405-1.621 50.809-3.785 76.482-4.596v41.617l-119.724 9.461v-255.39h119.724v43.241h-76.482v58.105zm237.284-58.104h-44.862v198.908c-14.594 0-29.188 0-43.239.539v-199.447h-44.862v-43.242h132.965l-.002 43.242zm70.266 55.132h59.187v43.24h-59.187v98.104h-42.433v-239.718h120.808v43.241h-78.375v55.133zm148.641 103.507c24.594.539 49.456 2.434 73.51 3.783v42.701c-38.646-2.434-77.293-4.863-116.75-5.676v-242.689h43.24v201.881zm109.994 49.457c13.783.812 28.377 1.623 42.43 3.242v-254.58h-42.43v251.338zm231.881-251.338l-54.863 131.615 54.863 145.127c-16.217-2.162-32.432-5.135-48.648-7.838l-31.078-79.994-31.617 73.51c-15.678-2.705-30.812-3.516-46.484-5.678l55.672-126.75-50.269-129.992h46.482l28.377 72.699 30.27-72.699h47.295z" />
                    </Logo>
                </Link>
            </List>
            <List><Link to={'/'}>홈{location === "/" ? <Circle layoutId="circle" /> : null}</Link></List>
            <List><Link to={'/tv'}>TV{location === "/tv" ? <Circle layoutId="circle" /> : null}</Link></List>
        </UList>
        <UList>
            <List>
                <AnimatePresence>
                    {searchState &&
                        <Wrapper onSubmit={handleSubmit(onSearch)}variants={searchVar} initial="initial" animate="click" exit="exit">
                            <Icon><TiZoomOutline size={24} /></Icon>
                            <Input {...register("keyword")} variants={searchVar} initial="initial" animate="click" placeholder="제목, 사람, 장르" />
                        </Wrapper>}
                </AnimatePresence>
                {!searchState && <Icon initial={{ x: -100, scaleX: 0.1, opacity: 0 }}
                animate={{ x: 0, scaleX: 1, opacity: 1, transition: { duration: 0.5 } }}
                onClick={() => setSearchState(cur => !cur)}><TiZoomOutline size={24} /></Icon>}
            </List>
        </UList>
    </Nav>;
}

export default Header;
